@use 'sass-utils' as *;

$breakpoints: (
  '': null,
  '-lg': $lg,
  '-md': $md,
  '-sm': $sm,
);

:root {
  --cols-gap: 0px;
  --cols-column-gap: var(--cols-gap);
  --cols-row-gap: var(--cols-gap);
}

.cols {
  display: grid;
  // flex-flow: wrap;
  row-gap: var(--cols-row-gap);
  column-gap: var(--cols-column-gap);

  // & > * {
  //   box-sizing: border-box;
  // }
}

@each $suffix, $breakpoint in $breakpoints {
  @for $columns from 1 through 4 {
    .cols#{$suffix}-#{$columns} {
      @include below($breakpoint) {
        grid-template-columns: repeat($columns, minmax(0, 1fr));
        // & > * {
        //   width: calc(100% / $columns - var(--cols-column-gap, 0) * ($columns - 1) / $columns);
        // }
      }
    }
  }
}
